import Konva from "konva";
import { Piece } from "./Piece";
import { Slot } from "./Slot";
import { PlayerType } from "./PlayerType";

export const CONFIG = {
  BOARD: {
    GAP: 8,
  },
  SLOT: {
    WIDTH: 100,
    HEIGHT: 62,
  },
  CELL: {
    WIDTH: 100,
    HEIGHT: 62,
  },
  COLOR: {
    DEFAULT: "#344e41",
  }
}

export class Board {
  socket = null;
  stage = null;
  layer = new Konva.Layer();
  x = 0;
  y = 0;
  row = 0;
  col = 0;
  width = 0;
  height = 0;
  clickedPiece = null;
  playerType = PlayerType.WHITE;
  orientation = PlayerType.WHITE;

  pieces = [];
  slots = []

  constructor(socket, container, x, y, row, col) {
    this.socket = socket;
    this.x = x;
    this.y = y;
    this.col = col;
    this.row = row;
    this.width = this.col * (CONFIG.SLOT.WIDTH + CONFIG.BOARD.GAP) + CONFIG.BOARD.GAP;
    this.height = this.row * (CONFIG.SLOT.HEIGHT + CONFIG.BOARD.GAP) + CONFIG.BOARD.GAP;

    this.stage = new Konva.Stage({
      container,
      width: this.width,
      height: this.height,
    });

    this.init();
  }

  init() {
    this.initKonva();
    this.initSockets();


    window.addEventListener('resize', () => {
      this.stage.width(this.width);
      this.stage.height(this.width);
    })
  }

  initKonva() {
    const boardRect = new Konva.Rect({
      width: this.width,
      height: this.height,
      fill: CONFIG.COLOR.DEFAULT,
    });

    this.layer.add(boardRect);

    for (let row = 0; row < this.row; row++) {
      const ROW = []
      for (let col = 0; col < this.col; col++) {
        const playerType = row < this.row / 2 ? PlayerType.WHITE : PlayerType.BLACK;

        ROW.push(new Slot(this, col, row, playerType));
      }
      this.slots.push(ROW);
    }

    this.stage.add(this.layer);

    this.initEvents();
  }

  initEvents() {
    
  }

  initSockets() {
    this.socket.on("sync-board", (data) => {
      this.loadBoard(data);
    });

    this.socket.on("opponent-moved-piece", ({ payload, result }) => {
      const { col: col1, row: row1 } = this.orientedColRow(payload.col1, payload.row1);
      const { col: col2, row: row2 } = this.orientedColRow(payload.col2, payload.row2);

      this.handlePieceBattle(col1, row1, col2, row2, result);
    });

    this.socket.on("winner", (data) => {
      console.log(data);
    })
  }

  movePiece(col1, row1, col2, row2) {
    if (!this.hasPieceAt(col1, row1)) {
      return;
    }

    this.pieces[row2][col2] = this.pieces[row1][col1];
    this.pieces[row1][col1] = null;

    this.pieces[row2][col2].setBoardPosition(col2, row2);
  }

  killPiece(col, row) {
    if (this.hasPieceAt(col, row)) {
      this.pieces[row][col].destroy();
    }

    this.pieces[row][col] = null;
  }

  handlePieceBattle(col1, row1, col2, row2, result) {
    if (result == -1) {
      this.killPiece(col1, row1);
    } else if (result == 0) {
      this.killPiece(col1, row1);
      this.killPiece(col2, row2);
    } else if (result == 1) {
      this.killPiece(col2, row2);
      this.movePiece(col1, row1, col2, row2);
    } else if (result == 2) {
      this.movePiece(col1, row1, col2, row2);
    }
  }

  handlePieceClick(col, row) {
    if (this.clickedPiece != null) {
      if (this.clickedPiece != this.pieces[row][col]) {
        // this.unclickPiece();
        // this.clickPiece(col, row);
        this.handleSlotClick(col, row);
      } else {
        this.unclickPiece();
      }
    } else {
      this.clickPiece(col, row);
    }
  }

  clickPiece(col, row) {
    if (this.pieces[row][col]) {
      if (this.pieces[row][col].pieceType.getValue() >= 0) {
        this.clickedPiece = this.pieces[row][col];
      }
    }
    
    if (this.clickedPiece != null) {
      this.setPotentialsOf(col, row, true);
    }
  }

  unclickPiece() {
    if (this.clickedPiece != null) {
      const { col, row } = this.clickedPiece.boardPosition;
      this.setPotentialsOf(col, row, false);
    }

    this.clickedPiece = null;
  }

  setPotentialsOf(col, row, state) {
    if (!this.hasPieceAt(col, row)) {
      return;
    }

    const dcol = [ 1, 0, -1, 0 ];
    const drow = [ 0, -1, 0, 1 ];

    for (let i = 0; i < 4; i++) {
      const col1 = col + dcol[i];
      const row1 = row + drow[i];

      if (this.isInBound(col1, row1)) {
        if (state) {
          if (this.hasPieceAt(col1, row1)) {
            if (this.pieces[row][col].playerType != this.pieces[row1][col1].playerType) {
              this.slots[row1][col1].setPotential(true);
            }
          } else {
            this.slots[row1][col1].setPotential(true);
          }
        } else {
          this.slots[row1][col1].setPotential(false);
        }
      }
    }
  }

  handleSlotClick(col, row) {
    if (this.clickedPiece != null) {
      const { col: pCol, row: pRow } = this.clickedPiece.boardPosition;
      const distance = this.getDistance(pCol, pRow, col, row);
      const { col: col1, row: row1 } = this.orientedColRow(pCol, pRow);
      const { col: col2, row: row2 } = this.orientedColRow(col, row);

      this.unclickPiece();

      if (distance == 1) {
        this.socket.emit("move-piece", {
          col1,
          row1,
          col2,
          row2,
        }, (result) => {
          this.handlePieceBattle(pCol, pRow, col, row, result);
        });
      }
    }

    this.unclickPiece();
  }

  getScreenPosition(col, row) {
    return {
      x: col * (CONFIG.SLOT.WIDTH + CONFIG.BOARD.GAP) + CONFIG.BOARD.GAP,
      y: row * (CONFIG.SLOT.HEIGHT + CONFIG.BOARD.GAP) + CONFIG.BOARD.GAP,
    }
  }

  getDistance(col1, row1, col2, row2) {
    return Math.abs(col1 - col2) + Math.abs(row1 - row2);
  }

  isInBound(col, row) {
    return col >= 0 && col < this.col && row >= 0 && row < this.row;
  }

  hasPieceAt(col, row) {
    return this.isInBound(col, row) && this.pieces[row][col] != null;
  }

  changePlayerType(playerType) {
    this.playerType = playerType;

    this.changeOrientation(this.playerType);
  }

  changeOrientation(orientation) {
    this.orientation = orientation;

    for (let row = 0; row < this.row; row++) {
      for (let col = 0; col < this.col; col++) {
        const playerType = row < this.row / 2 ? PlayerType.WHITE : PlayerType.BLACK;
        const oriented = this.orientation == PlayerType.WHITE ? playerType : (playerType == PlayerType.WHITE ? PlayerType.BLACK : PlayerType.WHITE);

        this.slots[row][col].changePlayerType(oriented);
      }
    }
  }

  orientedColRow(col, row) {
    if (this.orientation == PlayerType.BLACK) {
      return {
        col,
        row: this.row - 1 - row,
      }
    }

    return {
      col,
      row,
    };
  }

  syncBoardData({ data }) {
    const { col, row, pieces } = data;

    for (let i = 0; i < row; i++) {
      for (let j = 0; j < col; j++) {
        if (this.hasPieceAt(j, i) == (pieces[i][j] != null)) {
          if (this.hasPieceAt(j, i)) {
            const p1 = this.getPieceAt(j, i);
            const p2 = pieces[i][j];

            if (p1.pieceType.getId() != p2.pieceType.id
              || p1.playerType != p2.playerType
              || p2.x != j
              || p2.y != i) {

              this.loadBoard(data);
              return;
            }
          }
        } else {
          this.loadBoard(data);
          return;
        }
      }
    }
  }

  loadBoard({ data }) {
    this.unclickPiece();

    const { col: c, row: r, playerType, pieces } = data;

    if (playerType != null) {
      this.changePlayerType(playerType);
    }

    for (const ROW of this.pieces) {
      for (const piece of ROW) {
        if (piece != null) {
          piece.destroy();
        }
      }
    }

    this.pieces = [];

    for (let row = 0; row < r; row++) {
      const ROW = [];
      for (let col = 0; col < c; col++) {
        const { col: col1, row: row1 } = this.orientedColRow(col, row);

        const serialized = pieces[row1][col1];

        if (serialized != null) {
          const piece = Piece.deserialize(this, col, row, serialized);
          ROW.push(piece);
        } else {
          ROW.push(null);
        }
      }

      this.pieces.push(ROW);
    }
  }
}