import ImageLoader from "../utils/ImageLoader";

export const CONFIG = {
  PATH: {
    SVG: "svg/",
  }
}

export class PieceType {
  id = null;
  tag = null;
  value = null;
  svg = null;

  constructor(id, tag, value) {
    this.id = id;
    this.tag = tag;
    this.value = value;

    ImageLoader.load(`${ CONFIG.PATH.SVG }${ this.id }.svg`, (svg) => {
      this.svg = svg;
    });
  }

  getId() {
    return this.id;
  }

  getTag() {
    return this.tag;
  }

  getValue() {
    return this.value;
  }

  getSvg() {
    return this.svg;
  }

  compare(b) {
    return PieceType.compare(this, b);
  }

  static compare(a, b) {
    if (a.getValue() > b.getValue()) {
      return 1;
    } else if (a.getValue() < b.getValue()) {
      return -1;
    } else {
      return 0;
    }
  }

  static desearilize({ value }) {
    return getTypeByValue(value);
  }
}

export const Types = {
  HIDDEN: new PieceType("HIDDEN", "", -1),
  FLAG: new PieceType("FLAG", "FLAG", 0),
  PRIVATE:  new PieceType("PRIVATE", "PRIVATE", 1),
  SERGEANT: new PieceType("SERGEANT", "SERGEANT", 2),
  LIEUTENANT2: new PieceType("LIEUTENANT2", "2nd LIEUT.", 3),
  LIEUTENANT1: new PieceType("LIEUTENANT1", "1st LIEUT.", 4),
  CAPTAIN: new PieceType("CAPTAIN", "CAPTAIN", 5),
  MAJOR: new PieceType("MAJOR", "MAJOR", 6),
  LIEUTENANT_COLONEL: new PieceType("LIEUTENANT_COLONEL", "LT. COLONEL", 7),
  COLONEL: new PieceType("COLONEL", "COLONEL", 8),
  GENERAL1: new PieceType("GENERAL1", "GENERAL", 9),
  GENERAL2: new PieceType("GENERAL2", "GENERAL", 10),
  GENERAL3: new PieceType("GENERAL3", "GENERAL", 11),
  GENERAL4: new PieceType("GENERAL4", "GENERAL", 12),
  GENERAL5: new PieceType("GENERAL5", "GENERAL", 13),
  SPY: new PieceType("SPY", "SPY", 14),
};

export const getTypeByValue = (value) => {
  for (const obj of Object.entries(Types)) {
      if (obj[1].getValue() == value) {
          return obj[1];
      }
  }

  return null;
};

export const getTypeById = (id) => {
    for (const obj of Object.entries(Types)) {
        if (obj[1].getId() == id) {
            return obj[1];
        }
    }
  
    return null;
}