<template>
  <div class="game">
      <!-- <p v-if="opponent.username">OPPONENT: {{ opponent.username }}</p> -->
      <div id="game-container"></div>
      <!-- <p>{{ username }}</p> -->
  </div>
</template>

<script>
import { Board } from '@/game/Board'
import { mapGetters } from 'vuex';

export default {
  name: 'Game',
  data() {
    return {
      opponent: {},
    }
  },
  mounted() {
    new Board(this.$socket.client, 'game-container', 0, 0, 8, 9);
    
    this.$socket.client.emit("join-game", {
      gameId: this.$route.query.gameId,
      userId: this.userId,
    }, () => {
      this.$router.push({ name: 'Lobby' });
    });

    this.$socket.client.on("set-opponent", (opponent) => {
      this.opponent = opponent;
    });
  },
  computed: {
    ...mapGetters(['username', 'userId']),
  },
  methods: {
  }
}
</script>

