import Konva from "konva";
import { CONFIG as BOARD_CONFIG } from "../Board";
import { BoardComponent } from "../BoardComponent";
import { getTypeById } from "./PieceType";
import { PlayerType } from "../PlayerType";
// import ImageLoader from "../utils/ImageLoader";

export const CONFIG = {
  COLOR: {
    [PlayerType.WHITE]: {
      DEFAULT: "#f5fdc6",
      DARK: "#d2d9a9"
    },
    [PlayerType.BLACK]: {
      DEFAULT: "#36453b",
      DARK: "#2a362e"
    }
  },
  ID: {
    RECT: "piece-rect",
    SVG: "piece-svg",
    TAG: "piece-tag",
  }
}

export class Piece extends BoardComponent {
  playerType = null;
  pieceType = null;

  constructor(board, col, row, playerType, pieceType) {
    super(board, col, row);

    this.playerType = playerType;
    this.pieceType = pieceType;

    this.init();
  }

  init() {
    this.initKonva();
  }

  initKonva() {
    const offset = 4;
    const piece = new Konva.Rect({
      width: BOARD_CONFIG.SLOT.WIDTH - offset * 2,
      height: BOARD_CONFIG.SLOT.HEIGHT - offset * 2,
      fill: CONFIG.COLOR[this.playerType].DEFAULT,
      ID: CONFIG.ID.RECT,
      offset: {
        x: -offset,
        y: -offset,
      }
    });

    const tag = new Konva.Text({
      id: CONFIG.ID.TAG,
      text: this.pieceType.getTag(),
      width: BOARD_CONFIG.SLOT.WIDTH,
      y: BOARD_CONFIG.SLOT.HEIGHT - 15,
      align: 'center',
    });

    this.group.add(piece);
    this.group.add(tag);

    // ImageLoader.load(`${ PIECETYPE_CONFIG.PATH.SVG }${ this.pieceType.getId() }.svg`, (image) => {
    //   const svg = new Konva.Image({
    //     id: CONFIG.ID.SVG,
    //     image,
    //     width: 50,
    //     height: 50,
    //     offset: {
    //       x: -(BOARD_CONFIG.SLOT.WIDTH - 50) / 2,
    //       y: -(BOARD_CONFIG.SLOT.HEIGHT - 50) / 2 + 8,
    //     },
    //   });

    //   this.group.add(svg);
    // });
    this.board.layer.add(this.group);
    this.initEvents();
  }

  initEvents() {
    this.group.on('click', () => {
      this.board.handlePieceClick(this.boardPosition.col, this.boardPosition.row);
    })
  }

  static deserialize(board, col, row, serialized) {
    const piece = new Piece(board, col, row, serialized.playerType, getTypeById(serialized.pieceType.id));
    return piece;
  }
}