import Konva from "konva";
import { CONFIG as BOARD_CONFIG } from "../Board";
import { BoardComponent } from "../BoardComponent";
import { PlayerType } from "../PlayerType";

export const CONFIG = {
  COLOR: {
    [PlayerType.WHITE]: {
      DEFAULT: "#588157",
      POTENTIAL: "#a59132",
      POTENTIAL_DARK: "#a59132",
    },
    [PlayerType.BLACK]: {
      DEFAULT: "#a3b18a",
      POTENTIAL: "#a59132",
      POTENTIAL_DARK: "#a59132",
    },
  },
  ID: {
    RECT: "slot-rect",
  },
}

export class Slot extends BoardComponent {
  playerType = null;

  constructor(board, col, row, playerType) {
    super(board, col, row);

    this.playerType = playerType;

    this.init();
  }

  init() {
    this.initKonva();
  }

  initKonva() {
    const slot = new Konva.Rect({
      width: BOARD_CONFIG.SLOT.WIDTH,
      height: BOARD_CONFIG.SLOT.HEIGHT,
      fill: CONFIG.COLOR[this.playerType].DEFAULT,
      id: CONFIG.ID.RECT,
    });

    this.group.add(slot);

    this.initEvents();

    this.board.layer.add(this.group);
  }

  initEvents() {
    this.group.on('click', () => {
      this.board.handleSlotClick(this.boardPosition.col, this.boardPosition.row);
    });
  }

  setPotential(state) {
    const shape = this.getShape(CONFIG.ID.RECT);
    
    if (shape == null) {
      return;
    }

    if (state) {
      shape.fill(CONFIG.COLOR[this.playerType].POTENTIAL);
    } else {
      shape.fill(CONFIG.COLOR[this.playerType].DEFAULT);
    }
  }

  changePlayerType(playerType) {
    const slot = this.getShape(CONFIG.ID.RECT);

    this.playerType = playerType;

    slot.fill(CONFIG.COLOR[this.playerType].DEFAULT);
  }
}