import Konva from "konva";

export class BoardComponent {
  boardPosition = { col: 0, row: 0 };
  board = null;
  tweens = new Map();
  group = new Konva.Group();

  constructor(board, col, row) {
    this.board = board;

    this.setBoardPosition(col, row);

    this.updateScreenPosition();
  }

  setBoardPosition(col, row) {
    this.boardPosition.col = col;
    this.boardPosition.row = row;

    this.updateScreenPosition();
  }

  updateScreenPosition() {
    const screenPosition = this.board.getScreenPosition(this.boardPosition.col, this.boardPosition.row);

    this.group.setPosition(screenPosition);
  }

  getScreenPosition() {
    return this.group.position;
  }

  getShape(id) {
    return this.group.findOne("#" + id);
  }

  addTween(id, tween) {
    this.tweens.set(id, tween);
  }

  tween(callback, ...tweens) {
    for (const tween of tweens) {
      if (this.tweens.has(tween)) {
        callback(this.tweens.get(tween));
      } else {
        console.warn(`Tween "${ tween }" does not exists."`);
      }
    }
  }

  destroy() {
    this.group.destroy();

    for (const key in this.tweens) {
      this.tweens.get(key).destroy();
    }

    this.tweens.clear();
  }
}