export default class ImageLoader {
  static images = new Map();
  static loaded = new Map();
  static callbacks = new Map();

  static load(path, callback) {
    if (this.images[path]) {
      if (this.loaded[path]) {
        callback(this.images[path]);
      } else {
        this.callbacks[path].push(callback);
      }
    } else {
      const image = new Image();

      const handleImage = () => {
        while (this.callbacks[path].length > 0) {
            const fn = this.callbacks[path].pop();
            fn(image);
        }

        this.loaded[path] = true;
      }

      image.addEventListener('load', handleImage);

      image.src = path;

      this.loaded[path] = false;
      this.images[path] = image;
      this.callbacks[path] = [ callback ];
    }
  }
}